////
/// Components
/// Content
////

@import 'utils/shared/layout';
@import 'props';

.Content {
  @include centered-width(get-layout-length(content));
  margin-bottom: get-spacing(loose);
  text-align: center;

  @media screen and (min-width: get-breakpoint(tablet)) {
    margin-bottom: get-spacing(fat);
  }

  @media screen and (min-width: get-breakpoint(tablet, wide)) {
    margin-bottom: get-spacing(fattest);
  }

  // --- Typographic elements
  p {
    @include ContentParagraph;
  }
}

.Content--sizeSmall {
  max-width: get-layout-length();
}

///
/// Helper text

.HelperText {
  @include HelperText;
}

.HelperBullet {
  margin-right: $spacing-unit;
  margin-left: $spacing-unit;
}

///
/// Badge
.Badge {
  display: inline-block;
  margin-top: get-spacing(tightest);
  padding: get-spacing(tightest) (get-spacing(tightest) + $spacing-unit);
  font-size: get-type-scale(badge);
  font-weight: 900;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  color: get-color(coal, darker);
  border: 0.2rem solid currentColor;
  border-radius: get-radius();
}

.Badge--sizeLarge {
  padding: (get-spacing(tightest) + $spacing-unit) get-spacing(tighter);
  font-size: get-type-scale(badge, large);
  letter-spacing: 0.3rem;
  border-width: 0.3rem;
}
