////
/// Components
/// Page
////

html {
  font-family: get-font-stack();

  ::selection {
    color: get-color(ash, light);
    background-color: get-color(coal, darker);
  }
}

body {
  cursor: get-cursor();
  background-color: get-color(brand, blue);

  @media screen and (min-width: get-breakpoint(tablet)) {
    padding: get-spacing(tighter);
    background-color: get-color(purple);
    background-image: linear-gradient(
      188deg,
      get-color(purple),
      get-color(blue),
      get-color(brand, yellow),
      get-color(red),
      get-color(purple),
      get-color(blue, light),
      get-color(aqua),
      get-color(yellow),
      get-color(red)
    );
  }

  // Resolve overscroll background color
  &::after {
    content: '';
    position: sticky;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    height: 0;
    box-shadow: 0 get-spacing(jumboest) 0 get-spacing(jumboest) get-color(purple);

    @media screen and (min-width: get-breakpoint(tablet)) {
      box-shadow: 0 get-spacing(jumboest) 0 get-spacing(jumboest) get-color(red);
    }
  }
}

main {
  @media screen and (min-width: get-breakpoint(tablet)) {
    border: get-border(section, tablet);
  }
}

///
/// Cursor styles
p,
li,
dd,
.Heading,
.Subheading {
  cursor: get-cursor(text);
}

a,
button {
  cursor: get-cursor(pointer);

  &:active {
    cursor: get-cursor(pointer, active);
  }
}

// Set `state` cursors to never used tags only so they can be fetched on page load,
// rather than the instance they are first requested...
.preload-cursors {
  @include visually-hidden;

  b {
    cursor: get-cursor(drag, active);
  }

  u {
    cursor: get-cursor(pointer, active);
  }

  a {
    cursor: get-cursor(rock, active);
  }
}
